<template>

    <v-row dense>
    <v-col cols="12">
      <v-card >
        <v-list >
          <v-list-item >

            <v-list-item-content >
              <v-list-item-title><v-btn :disabled="fav"  @click="realAll()">{{title}} ({{messages}}) </v-btn></v-list-item-title>
            </v-list-item-content>

            <v-list-item-action>
              <v-btn
                :class="fav ? 'red--text' : ''"
                icon
                @click="fav_m()"
              >
                <v-icon>mdi-email-open-outline</v-icon>
              </v-btn>
            </v-list-item-action>
          </v-list-item>
        </v-list>

        <v-divider></v-divider>
        <div
            v-if="messages <= 0"
            class="mt-6 pb-6 ml-3 mr-3"
        >
            <v-row align="center"
                justify="center"

                >
                    <p class="text-h6 black--text">
                     All Done
                    </p>
            </v-row>
            <v-row align="center"
                justify="center">
                    <v-col
                    xs="12"
                    sm="4"
                    md="4"
                    lg="4"
                    xl="4"
                    >
                    <v-img
                    :lazy-src="$baseUrl+'storage/icons/balloon.png'"
                    :src="$baseUrl+'storage/icons/balloon.png'"
                    ></v-img>
                    </v-col>
            </v-row>

        </div>
        <div
            v-else
            v-for="(item, key) in items"
            :key="key"
        >
            <template>
            <v-list-item dense :key="item.id">
                <v-list-item-action v-if="!fav">
                <v-btn
                    icon
                    small
                    @click="read(item.id)"
                >
                <v-icon color="green">
                    mdi-check
                </v-icon>
                </v-btn>
                </v-list-item-action>

                <v-list-item-content>
                <p class="text-caption text--primary text-justify">
                    {{item.data.title}}
                </p>
                <p class="text-caption text--secondary text-justify">
                    {{item.data.body}}
                </p>
                <!--
                <v-list-item-title class="text-caption text--secondary" >
                    <div  class="scroll-bar hide-scrollbar" v-html="item.data.html_body"></div>
                </v-list-item-title>
                -->

                <v-list-item-title class="text-caption text--disabled">
                    Create: {{formatDate(item.created_at)}}
                </v-list-item-title>
                <v-list-item-title class="text-caption text--disabled" v-if="item.read_at">
                    Read: {{formatDate(item.read_at)}}
                </v-list-item-title>
                </v-list-item-content>

                <v-list-item-action
                    v-if="item.data.url"
                >
                    <v-btn
                    icon
                    small
                    :to="item.data.url"
                    >
                        <v-icon small>
                            mdi-open-in-new
                        </v-icon>
                    </v-btn>
                </v-list-item-action>
            </v-list-item>

            <v-divider></v-divider>
            </template>
        </div>
      </v-card>
    </v-col>
    </v-row>
</template>
<script>
import EventBus  from "../../Helpers/event-bus";
import moment from "moment";
  export default {
    data: () => ({
      fav: false,
      menu: false,
      badgeShow: false,
      icon:'mdi-bell-outline',
      messages: 0,
      benched: 30,
      title: 'Unread',
      items: [],
    }),
    methods:{
        event() {
            EventBus.$emit('badgeShowNotification', {
                show: this.badgeShow,
            })
        },
        formatDate(date) {
            return moment(date).format('DD-MM-YYYY HH:mm:ss');
        },
        realAll(){
            this.axios
            .get("mark-all-read-notification")
            .then(() => {
                this.unreadNotifications()
            })
        },
        read(id){
            this.axios
            .get("mark-read-notification/"+id)
            .then(() => {
                this.unreadNotifications()
            })
        },
        unreadNotifications() {
            this.axios
            .get("unread-notifications")
            .then(response => {
                this.messages = response.data.count;
                this.items = response.data.all;
                if(this.messages > 0){this.badgeShow=true}else{this.badgeShow=false}
                this.event();
            })
        },
        readNotifications() {
            this.axios
            .get("read-notifications")
            .then(response => {
                this.messages = response.data.count;
                this.items = response.data.all;
                if(this.messages > 0){this.badgeShow=true}else{this.badgeShow=false}
            })
        },
        fav_m(){
            this.fav = !this.fav
            switch (this.fav) {
                case true:
                    this.messages = 0;
                    this.items = [];
                    this.title = 'View Read'
                    this.readNotifications()
                    break;
                default:
                    this.messages = 0;
                    this.items = [];
                    this.title = 'View Unread'
                    this.unreadNotifications()
                    break;
            }
        }

    },
    computed: {
    },
    mounted(){
        this.unreadNotifications()
    }
  }
</script>
<style >
.scroll {
    height: calc(100vh - 200px);
}
.scroll-bar {
width: auto;
overflow-x: scroll;
overflow-y: scroll;
white-space: nowrap;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.hide-scrollbar::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.hide-scrollbar {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}
</style>
